@media screen and (max-width: 576px) {
  .overlay .section.section__inputs {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 470px;
  }
  .overlay .section.section__temperature {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 470px;
  }
}
@media screen and (max-width: 450px) {
  .overlay .section.section__inputs {
    width: 300px;
  }
  .section.section__inputs input {
    width: 150px;
  }
  .section.section__inputs button {
    width: 70px;
    padding: 10px;
  }
  .overlay .section.section__temperature {
    width: 300px;
  }
  .section.section__temperature .temperature h1{
    font-size: 40px;
  }
}

.icon .fa-solid {
  font-size: 39px;
  margin-top: 15px;
}
